  transform: translate(-50%, -25%);
  width: 0;
}

.icon-menu::before {
  background: currentColor;
  box-shadow: 0 -.35em, 0 .35em;
  content: "";
  height: .1rem;
  width: 100%;
}

.icon-apps::before {
  background: currentColor;
  box-shadow: -.35em -.35em, -.35em 0, -.35em .35em, 0 -.35em, 0 .35em, .35em -.35em, .35em 0, .35em .35em;
  content: "";
  height: 3px;
  width: 3px;
}

.icon-resize-horiz::before,
.icon-resize-horiz::after,
.icon-resize-vert::before,
.icon-resize-vert::after {
  border: .1rem solid currentColor;
  border-bottom: 0;
  border-right: 0;
  content: "";
  height: .45em;
  width: .45em;
}

.icon-resize-horiz::before,
.icon-resize-vert::before {
  transform: translate(-50%, -90%) rotate(45deg);
}

.icon-resize-horiz::after,
.icon-resize-vert::after {
  transform: translate(-50%, -10%) rotate(225deg);
}

.icon-resize-horiz::before {
  transform: translate(-90%, -50%) rotate(-45deg);
}

.icon-resize-horiz::after {
  transform: translate(-10%, -50%) rotate(135deg);
}

.icon-more-horiz::before,
.icon-more-vert::before {
  background: currentColor;
  border-radius: 50%;
  box-shadow: -.4em 0, .4em 0;
  content: "";
  height: 3px;
  width: 3px;
}

.icon-more-vert::before {
  box-shadow: 0 -.4em, 0 .4em;
}

.icon-plus::before,
.icon-minus::before,
.icon-cross::before {
  background: currentColor;
  content: "";
  height: .1rem;
  width: 100%;
}

.icon-plus::after,
.icon-cross::after {
  background: currentColor;
  content: "";
  height: 100%;
  width: .1rem;
}

.icon-cross::before {
  width: 100%;
}

.icon-cross::after {
  height: 100%;
}

.icon-cross::before,
.icon-cross::after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.icon-check::before {
  border: .1rem solid currentColor;
  border-right: 0;
  border-top: 0;
  content: "";
  height: .5em;
  transform: translate(-50%, -75%) rotate(-45deg);
  width: .9em;
}

.icon-stop {
  border: .1rem solid currentColor;
  border-radius: 50%;
}

.icon-stop::before {
  background: currentColor;
  content: "";
  height: .1rem;
  transform: translate(-50%, -50%) rotate(45deg);
  width: 1em;
}

.icon-shutdown {
  border: .1rem solid currentColor;
  border-radius: 50%;
  border-top-color: transparent;
}

.icon-shutdown::before {
  background: currentColor;
  content: "";
  height: .5em;
  top: .1em;
  width: .1rem;
}

.icon-refresh::before {
  border: .1rem solid currentColor;
  border-radius: 50%;
  border-right-color: transparent;
  content: "";
  height: 1em;
  width: 1em;
}

.icon-refresh::after {
  border: .2em solid currentColor;
  border-left-color: transparent;
  border-top-color: transparent;
  content: "";
  height: 0;
  left: 80%;
  top: 20%;
  width: 0;
}

.icon-search::before {
  border: .1rem solid currentColor;
  border-radius: 50%;
  content: "";
  height: .75em;
  left: 5%;
  top: 5%;
  transform: translate(0, 0) rotate(45deg);
  width: .75em;
}

.icon-search::after {
  background: currentColor;
  content: "";
  height: .1rem;
  left: 80%;
  top: 80%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: .4em;
}

.icon-edit::before {
  border: .1rem solid currentColor;
  content: "";
  height: .4em;
  transform: translate(-40%, -60%) rotate(-45deg);
  width: .85em;
}

.icon-edit::after {
  border: .15em solid currentColor;
  border-right-color: transparent;
  border-top-color: transparent;
  content: "";
  height: 0;
  left: 5%;
  top: 95%;
  transform: translate(0, -100%);
  width: 0;
}

.icon-delete::before {
  border: .1rem solid currentColor;
  border-bottom-left-radius: .1rem;
  border-bottom-right-radius: .1rem;
  border-top: 0;
  content: "";
  height: .75em;
  top: 60%;
  width: .75em;
}

.icon-delete::after {
  background: currentColor;
  box-shadow: -.25em .2em, .25em .2em;
  content: "";
  height: .1rem;
  top: .05rem;
  width: .5em;
}

.icon-share {
  border: .1rem solid currentColor;
  border-radius: .1rem;
  border-right: 0;
  border-top: 0;
}

.icon-share::before {
  border: .1rem solid currentColor;
  border-left: 0;
  border-top: 0;
  content: "";
  height: .4em;
  left: 100%;
  top: .25em;
  transform: translate(-125%, -50%) rotate(-45deg);
  width: .4em;
}

.icon-share::after {
  border: .1rem solid currentColor;
  border-bottom: 0;
  border-radius: 75% 0;
  border-right: 0;
  content: "";
  height: .5em;
  width: .6em;
}

.icon-flag::before {
  background: currentColor;
  content: "";
  height: 1em;
  left: 15%;
  width: .1rem;
}

.icon-flag::after {
  border: .1rem solid currentColor;
  border-bottom-right-radius: .1rem;
  border-left: 0;
  border-top-right-radius: .1rem;
  content: "";
  height: .65em;
  left: 60%;
  top: 35%;
  width: .8em;
}

.icon-bookmark::before {
  border: .1rem solid currentColor;
  border-bottom: 0;
  border-top-left-radius: .1rem;
  border-top-right-radius: .1rem;
  content: "";
  height: .9em;
  width: .8em;
}

.icon-bookmark::after {
  border: .1rem solid currentColor;
  border-bottom: 0;
  border-left: 0;
  border-radius: .1rem;
  content: "";
  height: .5em;
  transform: translate(-50%, 35%) rotate(-45deg) skew(15deg, 15deg);
  width: .5em;
}

.icon-download,
.icon-upload {
  border-bottom: .1rem solid currentColor;
}

.icon-download::before,
.icon-upload::before {
  border: .1rem solid currentColor;
  border-bottom: 0;
  border-right: 0;
  content: "";
  height: .5em;
  transform: translate(-50%, -60%) rotate(-135deg);
  width: .5em;
}

.icon-download::after,
.icon-upload::after {
  background: currentColor;
  content: "";
  height: .6em;
  top: 40%;
  width: .1rem;
}

.icon-upload::before {
  transform: translate(-50%, -60%) rotate(45deg);
}

.icon-upload::after {
  top: 50%;
}

.icon-time {
  border: .1rem solid currentColor;
  border-radius: 50%;
}

.icon-time::before {
  background: currentColor;
  content: "";
  height: .4em;
  transform: translate(-50%, -75%);
  width: .1rem;
}

.icon-time::after {
  background: currentColor;
  content: "";
  height: .3em;
  transform: translate(-50%, -75%) rotate(90deg);
  transform-origin: 50% 90%;
  width: .1rem;
}

.icon-mail::before {
  border: .1rem solid currentColor;
  border-radius: .1rem;
  content: "";
  height: .8em;
  width: 1em;
}

.icon-mail::after {
  border: .1rem solid currentColor;
  border-right: 0;
  border-top: 0;
  content: "";
  height: .5em;
  transform: translate(-50%, -90%) rotate(-45deg) skew(10deg, 10deg);
  width: .5em;
}

.icon-people::before {
  border: .1rem solid currentColor;
  border-radius: 50%;
  content: "";
  height: .45em;
  top: 25%;
  width: .45em;
}

.icon-people::after {
  border: .1rem solid currentColor;
  border-radius: 50% 50% 0 0;
  content: "";
  height: .4em;
  top: 75%;
  width: .9em;
}

.icon-message {
  border: .1rem solid currentColor;
  border-bottom: 0;
  border-radius: .1rem;
  border-right: 0;
}

.icon-message::before {
  border: .1rem solid currentColor;
  border-bottom-right-radius: .1rem;
  border-left: 0;
  border-top: 0;
  content: "";
  height: .8em;
  left: 65%;
  top: 40%;
  width: .7em;
}

.icon-message::after {
  background: currentColor;
  border-radius: .1rem;
  content: "";
  height: .3em;
  left: 10%;
  top: 100%;
  transform: translate(0, -90%) rotate(45deg);
  width: .1rem;
}

.icon-photo {
  border: .1rem solid currentColor;
  border-radius: .1rem;
}

.icon-photo::before {
  border: .1rem solid currentColor;
  border-radius: 50%;
  content: "";
  height: .25em;
  left: 35%;
  top: 35%;
  width: .25em;
}

.icon-photo::after {
  border: .1rem solid currentColor;
  border-bottom: 0;
  border-left: 0;
  content: "";
  height: .5em;
  left: 60%;
  transform: translate(-50%, 25%) rotate(-45deg);
  width: .5em;
}

.icon-link::before,
.icon-link::after {
  border: .1rem solid currentColor;
  border-radius: 5em 0 0 5em;
  border-right: 0;
  content: "";
  height: .5em;
  width: .75em;
}

.icon-link::before {
  transform: translate(-70%, -45%) rotate(-45deg);
}

.icon-link::after {
  transform: translate(-30%, -55%) rotate(135deg);
}

.icon-location::before {
  border: .1rem solid currentColor;
  border-radius: 50% 50% 50% 0;
  content: "";
  height: .8em;
  transform: translate(-50%, -60%) rotate(-45deg);
  width: .8em;
}

.icon-location::after {
  border: .1rem solid currentColor;
  border-radius: 50%;
  content: "";
  height: .2em;
  transform: translate(-50%, -80%);
  width: .2em;
}

.icon-emoji {
  border: .1rem solid currentColor;
  border-radius: 50%;
}

.icon-emoji::before {
  border-radius: 50%;
  box-shadow: -.17em -.15em, .17em -.15em;
  content: "";
  height: .1em;
  width: .1em;
}

.icon-emoji::after {
  border: .1rem solid currentColor;
  border-bottom-color: transparent;
  border-radius: 50%;
  border-right-color: transparent;
  content: "";
  height: .5em;
  transform: translate(-50%, -40%) rotate(-135deg);
  width: .5em;
}

::-webkit-scrollbar {
  width: 2px;
  height: 2px;
}

::-webkit-scrollbar-button {
  width: 0px;
  height: 0px;
}

::-webkit-scrollbar-thumb {
  background: #e1e1e1;
  border: 0px none #fff;
  border-radius: 50px;
}

::-webkit-scrollbar-thumb:hover {
  background: #fff;
}

::-webkit-scrollbar-thumb:active {
  background: #666;
}

body {
  font-family: Helvetica, Arial, sans-serif;
  overflow-x: hidden;
  background-color: #fafafa;
}

header {
  height: 50px;
  color: white;
}

a.navbar-brand.mr-2 {
  font-size: 1.1em;
  font-weight: bold;
  margin: 20px 0 0 1.35em;
}

a.navbar-brand.mr-2:hover {
  text-decoration: none;
}

.tab.tab-block {
  color: #5755d9;
}

.tab.tab-block .active {
  background-color: #5755d9;
}

.tab .tab-item a.active {
  color: #fff;
}

#entities {
  /* background: #fff; */
  /* shadowy border */
  /* box-shadow: 0 0 20px #666;
    -webkit-box-shadow: 0 0 20px #666;
    -moz-box-shadow: 0 0 20px #666; */
}

#queryForm {
  /* display: block; */
  /* text-align: center; */
  /* margin: 0.25em 0; */
}

#query {
  width: 91%;
  height: 24px;
}

#search-view {
  width: 100%;
  margin-bottom: 50px;
}

#library-view {
  margin-bottom: 50px;
}

#library tr {
  list-style: none;
  padding: 4px 8px;
  margin: 0;
  cursor: pointer;
}

#library tr.selected {
  color: #5755d9;
}

#library tr .playing {
  margin-left: 5px;
  font-size: 0.9em;
}

#main-detail,
#extra-detail {
  /* position: fixed;
    left: 17em; */
  margin: 1.0em 0 0 1.5em;
}

#main-detail {
  /* top: 36px;
    height: 98px; */
}

#main-detail .artist,
#main-detail .album,
#main-detail .title {
  display: block;
  text-align: center;
}

#main-detail .title {
  font-size: 1.3em;
  font-weight: bold;
}

#main-detail .albumtitle {
  font-style: italic;
}

#extra-detail {
  /* overflow-x: hidden;
    overflow-y: auto;
    top: 134px;
    bottom: 0;
    right: 0; */
}

/*Fix for correctly displaying line breaks in lyrics*/
#extra-detail .lyrics {
  white-space: pre-wrap;
}

#extra-detail dl dt,
#extra-detail dl dd {
  list-style: none;
  margin: 0;
  padding: 0;
}

#extra-detail dl dt {
  width: 10em;
  float: left;
  text-align: right;
  font-weight: bold;
  clear: both;
}

 #extra-detail dl dd {
  margin-left: 10.5em;
}

#player {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  background: #fff;
  z-index: 99;
  /* shadowy border */
  box-shadow: 0 -0px 10px #999;
  -webkit-box-shadow: 0 -0px 20px #999;
  -moz-box-shadow: 0 -0px 20px #999;
}

#player .play,
#player .pause,
#player .disabled {
  -webkit-appearance: none;
  font-size: 1em;
  font-family: Helvetica, Arial, sans-serif;
  background: none;
  border: none;
  color: white;
  /* padding: 5px; */
  margin: 0;
  text-align: center;
  width: 36px;
  height: 36px;
}

#player .disabled {
  color: #666;
}

/* Times */
#times {
  width: 50px;
}

/** Volume Slider **/
#volumeSlider {
  width: 100%;
}

.fleft {
  float: left;
}

.fright {
  float: right;
}

.no-decoration {
  text-decoration: none;
}

.no-decoration a:hover {
  text-decoration: none;
}

.align-center {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.m-top-15 {
  margin-top: 15px;
}

.m-top-10 {
  margin-top: 10px;
}

.p-top-15 {
  padding-top: 15px;
}

.more-button {
  margin-top: -10px;
  height: 20px;
  width: 20px;
}

.more-button i {
  margin-top: -5px;
}

#more-panel {
  height: 0;
  position: fixed;
  z-index: 1;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  overflow-x: hidden;
  overflow-y: hidden;
  padding-top: 60px;
  transition: 0.5s;
}

#more-panel a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

#more-panel a:hover {
  /* color: #575757; */
  color: #f1f1f1;
}

#more-panel .close-more-button {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

/* Keep at bottom */
.hidden {
  display: none !important;
}
